import { authService, dbService, storageService } from 'fbase';
import React, { useEffect, useState } from 'react';
import { useHistory } from "react-router-dom";
import './Profile.css';

const Profile = ({ userObj, refreshUser }) => {
    const history = useHistory();
    const [newName, setNewName] = useState(userObj.displayName);
    const [photo, setPhoto] = useState(userObj.photoURL);
    const [pbutton, setPbutton] = useState(false);
    const [cbutton, setCbutton] = useState(false);
    const [mypost, setMyPost] = useState([]);

    useEffect(() => {
        dbService.collection('posts').onSnapshot((s) => {
            let postArray = s.docs.map((d) => ({id : d.id, creator : d.creator , ...d.data()}));
            postArray = postArray.filter(i => i.creator === userObj.uid);
            setMyPost(postArray);
        })
    }, []);
    const onSubmit = async (e) => {
        let fileURL = '';

        e.preventDefault();
        if(userObj.photoURL !== photo){
            const fileRef = storageService.ref().child(`${userObj.uid}/photo`);
            const response = await fileRef.putString(photo, "data_url");
            fileURL = await response.ref.getDownloadURL();
            
            await userObj.updateProfile({ photoURL : fileURL });
            refreshUser();
        }
        if(userObj.displayName !== newName) {
            await userObj.updateProfile({ displayName : newName });
            refreshUser();
        }

        history.push('/');
    }
    const onChange = (e) => {
        const { target : {value} } = e;
        setNewName(value);
    }
    const onFileChange = (e) => {
        const{ target : { files } } = e;
        const theFile = files[0];
        const reader = new FileReader();

        reader.onloadend = (finishedE) => {
            const { currentTarget : {result} } = finishedE;
            setPhoto(result);
        }
        reader.readAsDataURL(theFile);
    }
    const onDelete = async () => {
        const ok = window.confirm(`ì§„ì§œ ì‚­ì œí•˜ì‹œê² ìŠµë‹ˆê¹Œ?\n(í™•ì¸ì„ ëˆ„ë¥´ë©´ ë¡œê·¸ì¸ í™”ë©´,\nì·¨ì†Œë¥¼ ëˆ„ë¥´ë©´ ë‚´ ë¸”ë¡œê·¸ í™ˆìœ¼ë¡œ ì´ë™í•©ë‹ˆë‹¤)`);
        if(ok) {
            await mypost.map((post) => { dbService.doc(`posts/${post.id}`).delete(); });
            const user = authService.currentUser;
            user.delete().then(() => {
                authService.delete();
            })
        } 
        history.push('/');     
    }
    
    return(
        <section className = 'boxes'> 
            <div className = 'pbutton'>
                <button className = 'b' onClick = {() => { setPbutton(prev => !prev); }}> í”„ë¡œí•„ </button>
                { pbutton &&
                    <div className = 'eentire'>
                        <form className = 'editbox' onSubmit = {onSubmit}>
                            <img className = 'image' src = {photo} width = '161px' height = '184px' alt={photo} />
                            <input className = 'image-upload'
                                type = 'file'
                                accept = 'image/*'
                                onChange = {onFileChange}
                            />
                            <input className = 'newnick'
                                type = 'newName'
                                value = {newName}
                                placeholder = 'ìƒˆ ë‹‰ë„¤ì„ì„ ì…ë ¥í•˜ì„¸ìš”'
                                onChange = {onChange}
                            /> 
                            <input className = 'newprofile' type = 'submit' value = 'ìˆ˜ì •' />
                            <button className = 'cancleprofile' onClick = {() => { setPbutton(prev => !prev); } }> ì·¨ì†Œ </button>
                        </form>
                    </div>}       
                
            </div>

            <div className = 'cbutton'>
                <button className = 'c' onClick = {() => { setCbutton(prev => !prev); }}> íšŒì› íƒˆí‡´ </button>
                { cbutton && 
                    <div>
                    <form className = 'editbox'>
                        <p className = 'deletext'>
                            ( í˜„ì¬ ë¡œê·¸ì¸ ì¤‘ì¸ ê³„ì • : {userObj.email} )<br/><br/>
                            íšŒì› íƒˆí‡´ì‹œ ì‘ì„±í•œ ê¸€ì€ ëª¨ë‘ ì‚­ì œë˜ë©°,<br/>
                            ì‚­ì œëœ ë¸”ë¡œê·¸ëŠ” ë³µêµ¬ê°€ ë¶ˆê°€ëŠ¥í•©ë‹ˆë‹¤.<br/>
                            ì •ë§ ì‚­ì œí•˜ì‹œê² ìŠµë‹ˆê¹Œ?<br/>
                        </p>
                        <button className = 'deleteblog' onClick = {() => { onDelete(); }}> í™•ì¸ </button>
                        <button className = 'cancledelete' onClick = {() => { setCbutton(prev => !prev); } }> ì·¨ì†Œ </button>
                    </form>
                    </div>}
            </div>
        </section>
    );
};

export default Profile;